.admin-header-right {
  padding: 0;
  height: 80px;
  background-color: #fff;
}
.admin-header-right .admin-header-top {
  text-align: right;
  height: 40px;
  line-height: 40px;
  border-bottom: 1px solid #1da57a;
}
.admin-header-right .admin-header-top span {
  height: 40px;
  margin: 0 15px;
}
.admin-header-right .admin-header-top a {
  height: 45px;
  margin: 0 30px 0 0;
}
.admin-header-right .admin-header-bottom {
  display: flex;
  align-items: center;
  height: 40px;
  line-height: 40px;
}
.admin-header-right .admin-header-bottom .admin-header-bottom-left {
  text-align: center;
  font-size: 20px;
  width: 20%;
  height: 40px;
  position: relative;
}
.admin-header-right .admin-header-bottom .admin-header-bottom-left::after {
  content: "";
  display: inline-block;
  position: absolute;
  top: 40px;
  -webkit-transform: translateX(50%);
          transform: translateX(50%);
  right: 50%;
  border-top: #fff solid 15px;
  border-right: #f0f2f5 solid 15px;
  border-left: #f0f2f5 solid 15px;
}
.admin-header-right .admin-header-bottom .admin-header-bottom-right {
  text-align: right;
  height: 40px;
  width: 80%;
}
.admin-header-right .admin-header-bottom .admin-header-bottom-right span {
  margin-right: 18px;
}
.admin-header-right .admin-header-bottom .admin-header-bottom-right img {
  width: 28px;
  margin-right: 18px;
}
