html,
body,
#root {
  width: 100%;
  height: 100%;
}
.login-container {
  width: 100%;
  height: 100%;
  background: url(/static/media/bg.ab45504b.jpg);
  background-size: 100% 100%;
}
.login-container .login-header {
  height: 80px;
  background: rgba(21, 20, 13, 0.5);
  display: flex;
  color: #fff;
  align-items: center;
}
.login-container .login-header img {
  height: 40px;
  margin: 0 20px 0 80px;
}
.login-container .login-header h1 {
  color: #fff;
  font-size: 30px;
}
.login-container .login-content .login-form-wrap {
  width: 400px;
  height: 300px;
  margin: 50px auto 0;
  padding: 20px 30px 40px 30px;
  background: #fff;
}
.login-container .login-content .login-form-wrap h2 {
  color: #000;
  font-size: 30px;
  font-weight: bold;
  text-align: center;
}
.login-container .login-content .login-form-wrap .login-form .login-form-button {
  width: 100%;
}
.login-container .login-content::before {
  content: "";
  display: table;
}
